import Image from './image'
import { Link } from 'gatsby'
import React from 'react'
import SpotifyIcon from "../content/icons/spotify.inline.svg"
import YouTubeIcon from "../content/icons/youtube.inline.svg"

export default function PlaylistItem ({ data }) {
    const { node } = data
    const { frontmatter, html } = node
  
    return (
      <div style={{ border: '1px solid rgba(0,0,0,0.12)', margin: '1em' }}>
          <h1>{`#strasznieMuzycznie • ${frontmatter.no} • ${frontmatter.title}`}</h1>
          <h2>{frontmatter.date}</h2>
          <div
            className="blog-post-content"
            dangerouslySetInnerHTML={{ __html: html }}
          />
          {/* <Image filename={`${frontmatter.no}.jpg`} /> */}
          <Link to={frontmatter.spotify}><SpotifyIcon /></Link>
          <br />
          <Link to={frontmatter.youtube}><YouTubeIcon /></Link>
          <Link to={frontmatter.slug}>Details</Link>
      </div>
    )
  }
